@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Press Start 2P', cursive;
}

body {
  background-image: url(images/grassbg.png);
  background-repeat: repeat;
}

.window-btn {
  font-family: 'Press Start 2P', cursive;
  font-size: 16px;
  padding: 10px;
  background-color: #FFD800;
  border-top: 5px solid #FFEC8E;
  border-left: 5px solid #FFEC8E;
  border-right: 5px solid #E5B116;
  border-bottom: 5px solid #E5B116;
  box-shadow: 0 0 0 5px #D87C2B;
}

.window-btn:active {
  background-color: #E5B116;
  border-top: 5px solid #FFD800;
  border-left: 5px solid #FFD800;
  border-right: 5px solid #FFEC8E;
  border-bottom: 5px solid #FFEC8E;
}

h1 {
  font-size: 18px;
}

.card {
  width: 120px;
  height: 170px;
  color: black;
  margin: 5px;
  border: none;
  font-size: 44px;
  background-image: url(images/cardup.png);
  background-position: center;

  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-timing-function: ease-out;
}

.card:hover {
  border: 2px solid black;
}

.card img {
  width: 73px;
  height: 73px;
}

.hidden {
  background-color: green;
  color: white;
  background-image: url(images/carddown.png);
}

.matched {
  visibility: hidden;
}

.board {
  width: 650px;
  min-width: 650px;
  height: 720px;
  padding: 10px;
  text-align: center;
  background-image: url(images/board.png);
}

.info {
  width: 230px;
  min-width: 230px;
  height: 720px;
  padding: 10px;
  text-align: center;
  background-image: url(images/info.png);
}

.info span, .info div {
  display: inline-block;
  margin: 16px 0px 16px 0px;
  font-size: 26px;
}

.window {
  width: 880px;
  min-width: 880px;
  height: 720px;
  padding: 10px 20px 10px 20px;
  position: absolute;
  z-index: 2;
  text-align: center;
  background-image: url(images/window.png);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 16px;
}

.window div {
  margin: 0px 140px 0px 140px;
}

.window p {
  margin: 80px 0px 80px 0px;
  line-height: 2;
}

.window-header {
  font-size: 32px;
  text-align: center;
  margin-bottom: 30px;
}

.game {
  /*width: 100%;
  height: 100vh;*/
  width: 920px;
  height: 740px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;  

	position: absolute;
	left: 50%;
	top: 50%;
	transform-origin: center center;
}

@keyframes flip {
  0%   {transform: scaleX(1)}
  50%  {transform: scaleX(0)}
  100% {transform: scaleX(1)}
}